body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#cards-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

#product-image {
  max-height: 250px;
  max-width: 250px;
}

#product-container {
  display: flex;
  align-items: flex-start;
  max-width: 1000px;
  margin: 30px auto;
}

#product-image {
  margin-right: 50px;
}

#product-details {
  text-align: left;
}

#product-container h2 {
  margin-top: 0;
}

#product-price {
  font-weight: bold;
}

#product-container {
  margin-top: 30px;
}

#page-content-container {
  text-align: center;
  width: 100%;
}

#page-and-cart-container {
  display: flex;
  height: 100%;
}

#app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

#category-cards-container {
  margin-top: 30px;
}

#cart-product-image {
  max-height: 80px;
  max-width: 80px;
  width: auto;
  height: auto;
}

#cart-product {
  display: flex;
  align-items: center;
  margin: 10px 0 15px 5px;
}

#cart-product-details {
  margin-left: 10px;
}

#cart-product-details p {
  font-size: 0.9em;
  margin: 3px;
}

#product-remove-button {
  color: blue;
}

#product-remove-button:hover {
  cursor: pointer;
}

#cart-product-image-container {
  min-width: 80px;
  text-align: center;
  margin-left: 4px;
}

#cart-product-title {
  font-weight: bold;
}
